import React, { Component } from 'react';

const especificacoes = [
    { item: "Cor", value: "Branco" },
    { item: "Marca", value: "Kolimar" },
    { item: "Uso Indicado", value: "Alvenaria, Concreto, Paredes" },
    { item: "Tonalidade", value: "Branco" },
    { item: "Tipo de Massa", value: "Massa Corrida" },
    { item: "Garantia", value: "De Acordo com o Código de Defesa do Consumidor" },
    { item: "Área Indicada", value: "Interno" },
    { item: "Volume", value: "26 Kg" },
    { item: "Tempo Médio de Secagem", value: "Ao toque: 2 hora\nFinal: 12 Horas" },
    { item: "Ferramenta Indicada para Aplicação", value: "Desempenadeira, Espátula" },
    { item: "Rendimento", value: "Barrica 26kg até 30m²/Demão" },
    { item: "Diluição", value: "Pronta para o uso" },
    { item: "Utilização", value: "Indicada para uniformizar, nivelar e corrigir pequenas imperfeições em superfícies internas de alvenaria e concreto." },
    { item: "Informações Complementares", value: "Preparo da superfície:\nAs superfícies a serem emassadas deverão estar livres de poeira, óleos, graxas e outras impurezas. Se o substrato apresentar mofo, o mesmo deverá ser removido através de jateamento de uma solução de água e água sanitária em partes iguais. Em caso de umidade, a mesma deverá ser eliminada com tratamento de impermeabilização. O perfeito desempenho do produto depende das condições de preparação da superfície, cabendo ao aplicador essa avaliação." },
    { item: "Conteúdo da Embalagem", value: "01 Barrica" },
    { item: "Embalagem", value: "26 Kg" },
    { item: "Certificação", value: "ISO 9001; Abrafati" }
];

class Descricao extends Component {

    state = { atual: "descricao" }
    onChange = (atual) => this.setState({ atual });

    renderHeader(){
        const { atual } = this.state;
        return(
            <div className="descricao-header">
                <div className={`descricao-item ${ atual === 'descricao' ? "descricao-item-active" : ""}`} onClick={() => this.onChange('descricao')}>
                    <span>Descrição</span>
                </div>
                <div className={`descricao-item ${ atual === 'especificacao' ? "descricao-item-active" : ""}`} onClick={() => this.onChange('especificacao')}>
                    <span>Especificação</span>
                </div>
            </div>
        )
    }

    renderBody(){
        return(
            <div className="descricao-body">
                <h3>Massa Corrida Kolimar 26kg </h3>
                <br />
                <p>A Massa Corrida da Kolimar é indicada para corrigir imperfeições das paredes e nivelar superfícies a serem pintadas em ambientes internos. Com boa aplicação, o produto está disponível em barrica de 26 Kg.</p>
                <br />
                <h3>Kolimar</h3>
                <br />
                <p>A Kolimar atua no mercado desde 1969, sendo uma das mais sólidas e reconhecidas no seguimento de tintas no estado do Rio de Janeiro, produzindo produtos com excelente padrão de qualidade e de maneira sustentável.</p>
                <p>A Kolimar é certificada pelo sistema de gestão da qualidade ISO 9001, além de possuir produtos certificados pelas normas ABNT NBR e pelo INMETRO.</p>
            </div>
        )
    }

    renderTable(){
        return (
            <div className="descricao-body">
                <table className="table">
                    <tbody>
                        {
                            especificacoes.map((t,idx)=>(
                                <tr key={idx}>
                                    <td>{t.item}</td>
                                    <td>{t.value}</td>
                                </tr>
                            ))
                        }
                    </tbody>
                </table>
            </div>
        )
    }

    render(){
        const { atual } = this.state;
        return (
            <div className="Descricao">
                <div className="container flex vertical">
                    <div className="card">
                        { this.renderHeader() }
                        { atual === 'descricao' && this.renderBody() }
                        { atual === 'especificacao' && this.renderTable() }
                    </div>
                </div>
            </div>
        )
    }
}

export default Descricao;